export async function loader() {
    return json({
        ENV: {
            DIRECTUS_URL: process.env.DIRECTUS_URL,
            DIRECTUS_EMAIL: process.env.DIRECTUS_EMAIL,
            DIRECTUS_PASSWORD: process.env.DIRECTUS_PASSWORD,
        },
    });
}

export const action = async ({ request }) => {
    const formData = await request.formData();
    const Error = formData?.get("error");
    if (Error === "Error: Invalid user credentials.") {
        await authLogout(request);
    }
    return "";
}

export function ErrorBoundary({ error }, req, res) {
    return (
        <div className="p-4 justify-center align-middle"
        style={{position:"relative",margin:"-8px",width:"calc(100% + 16px)",height:"100vh"}}
        >
        <HeaderFile login server={true} logoCenter={true}/>
        <div className="oops-message" style={{textAlign:"center",padding: "25px",height:"calc(100vh - 140px)",display:"flex",flexDirection:"column",justifyContent:"center",fontFamily:"Arial"}}>
            <h1 className="text-2xl" style={{fontFamily:"Arial"}}>Oops!! Something went wrong!</h1>
            <p style={{fontSize: "18px",padding:"10px 0",fontFamily:"Arial"}}>{error.message}</p>
            {error?.message === "Invalid user credentials." && (
            <Form
                className="max-w-[400px] w-full flex flex-col items-center text-[14px] "
                method="post"
                style={{paddingTop:"20px"}}
            >
                <input
                type="text"
                name="error"
                style={{ display: "none" }}
                value={error}
                onChange={() => {}}
                />
                <button type="submit" style={{textAlign:"center",padding: "16px 40px",backgroundColor:"#000022",color:"#fff",border: "none",boxShadow: "none",borderRadius: "5px",fontSize:"16px",cursor:"pointer"}}>Back to login</button>
            </Form>
            )}
        </div>
        </div>
    );
}

function App() {
    const data = useLoaderData();
    return (
        <html lang="en" suppressHydrationWarning={true}>
            <head>
                <meta charSet="utf-8" />
                <meta name="viewport" content="width=device-width,initial-scale=1" />
                <meta httpEquiv="content-type" content="text/html;charset=utf-8" />
                <meta name="robots" content="noindex, nofollow" />
                <meta name="googlebot" content="noindex"/>
                <link rel="icon" href={favicon}/>
                <Meta />
                <Links />
            </head>
            <body className="bg-white">
                <ToastContainer
                    position="bottom-center"
                    autoClose={2100}
                    hideProgressBar={true}
                    newestOnTop={false}
                    closeOnClick
                    rtl={false}
                    pauseOnFocusLoss
                    draggable
                    pauseOnHover
                // theme="dark"
                />
                <Outlet />
                <ScrollRestoration />
                <script
                    dangerouslySetInnerHTML={{
                        __html: `window.ENV = ${JSON.stringify(data.ENV)}`,
                    }}
                />
                <script src="//cdnjs.cloudflare.com/ajax/libs/alpinejs/3.10.5/cdn.js" />
                <Scripts />
                {/* {process.env.NODE_ENV === "development" && <LiveReload />} */}
            </body>
        </html>
    );
}
// export default withSentry(App);
export default App;